:root{--first-acent-color: #36a6de;--second-acent-color: #f3fafe;--white: #fff;--green: #45c4a0}body{margin:0;padding:0;background:#ebf8ff;background:linear-gradient(309deg, rgb(235, 248, 255) 0%, rgba(254, 255, 134, 0.5) 35%, rgba(130, 213, 255, 0.5) 100%)}.header{display:flex;align-items:center;justify-content:center;z-index:10;width:100%;height:80px;background:#36a6de;background:linear-gradient(65deg, rgba(54, 166, 222, 0.8) 0%, rgba(69, 196, 160, 0.8) 35%, rgba(54, 166, 222, 0.8) 80%);position:fixed;top:0;left:0;box-shadow:0 0 10px 1px rgba(0,0,0,.1)}.input-wrap{display:flex;justify-content:center;align-items:center;position:relative}.input{padding:9px;width:300px;outline:none;border:1px solid rgba(255,255,255,.3);border-radius:5px;transition:border-color 250ms cubic-bezier(0.4, 0, 0.2, 1)}.input:hover,.input:focus{border-color:var(--second-acent-color)}.search{position:absolute;display:flex;right:0;cursor:pointer;border:none;border-top-right-radius:5px;border-bottom-right-radius:5px;background-color:var(--green);color:var(--white);transition:color 250ms cubic-bezier(0.4, 0, 0.2, 1),background-color 250ms cubic-bezier(0.4, 0, 0.2, 1)}.search:hover{background-color:var(--first-acent-color)}.icon{display:inline-block;width:1em;height:1em;stroke-width:0;padding:10px 5px;stroke:currentColor;fill:currentColor}.gallery{display:flex;flex-wrap:wrap;justify-content:center;width:100%;gap:15px;margin-top:100px;margin-bottom:25px}.gallery--card{display:flex;flex-direction:column;border-radius:4px;text-decoration:none;transition:transform 250ms cubic-bezier(0.4, 0, 0.2, 1),box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);box-shadow:0px 1px 1px rgba(0,0,0,.12),0px 4px 4px rgba(0,0,0,.06),1px 2px 2px rgba(0,0,0,.16)}.gallery--card:hover{cursor:pointer;transform:scale(1.05);box-shadow:0px 1px 1px rgba(0,0,0,.12),0px 4px 4px rgba(0,0,0,.06),1px 4px 6px rgba(0,0,0,.16)}.gallery--image{display:block;flex-grow:1;object-fit:cover;width:320px;height:213px}.gallery--image:hover{cursor:zoom-in}.galery--info{display:flex;justify-content:space-around;background-color:var(--white)}.galery--item{display:flex;flex-direction:column-reverse;justify-content:center;align-items:center;line-height:1.2222222222;color:gray}.load-more{display:flex;justify-content:center;align-items:center;margin:0 auto;text-transform:uppercase;padding-top:5px;padding-right:15px;padding-bottom:5px;padding-left:15px;margin-bottom:25px;border:1px solid rgba(255,255,255,.3);border-radius:4px;cursor:pointer;background-color:var(--green);color:var(--white);transition:250ms cubic-bezier(0.4, 0, 0.2, 1)}.load-more:hover{transform:translateY(0.15rem);box-shadow:0rem .25rem .75rem rgba(30,34,40,.15);transition:all .2s ease-in-out}.btn-to-top{position:fixed;bottom:3vh;right:2vw;z-index:1;width:50px;height:50px;display:flex;align-items:center;justify-content:center;border-radius:50%;box-shadow:0 1px 2px 0 rgba(60,64,67,.3),0 2px 6px 2px rgba(60,64,67,.15);cursor:pointer;opacity:0;visibility:hidden;background-color:var(--first-acent-color);color:var(--white);transition:background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),color 250ms cubic-bezier(0.4, 0, 0.2, 1);transition:250ms cubic-bezier(0.4, 0, 0.2, 1)}.btn-to-top:hover{transform:translateY(-0.15rem);transition:250ms cubic-bezier(0.4, 0, 0.2, 1);background-color:var(--green);color:var(--white)}.btn-to-top--visible{opacity:1;visibility:visible}.visually-hidden{position:absolute;white-space:nowrap;width:1px;height:1px;overflow:hidden;border:0;padding:0;clip:rect(0 0 0 0);clip-path:inset(50%);margin:-1px}